.bookWrapper {
  box-sizing: border-box;
  padding: 20px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid rgba(0, 0, 0, 0.433);
  border-radius: 14px;
  transition: all ease-in-out 0.5s;
}

.bookWrapper:hover {
  scale: 1.05;
  cursor: pointer;
  box-shadow: 0px 1px 9px -3px rgba(39, 39, 39, 1);
}

.bookWrapper .bookHeader{
  display: flex;
  width: 100%;
  color: rgba(0, 0, 0, 0.433)
}

.bookWrapper .cover {
  width: 30%;
  height: 200px;
  margin-right: 20px;
}

.bookWrapper .cover img {
  width: 100%;
  height: 100%;
}

.bookWrapper .info {
  font-size: 1.1rem;
  color: rgba(0, 0, 0, 0.433);
  width: 50%;
} 

.bookWrapper .description{
  padding: 20px 0;

}